import Header from "../../components/Header.vue";
import Footer from "../../components/Footer.vue";
import Condividi from "../../components/Condividi.vue";
import DataHeader from "../../components/DataHeader.vue";
import { StringRenderer } from "../../../commons/js/string_renderer.js";
import { Pagina } from "../../../commons/js/pagina.js";
import { Argomento, vistaArgomenti } from "../../js/argomenti.js";
import { NotizieCorrelate } from "../../js/notizie-correlate.js";
import { Utils } from "../../../commons/js/utils";
import { AnteprimaImmagine, VistaGallerie, AnteprimaGalleria } from "../../js/gallerie";

export default {
    name: "app",
    components: {
        Footer,
        Header,
        Condividi,
        DataHeader
    },
    data: function () {
        $(document).ready(renderNotizia);
        return {};
    }
};

function renderNotizia() {
    var urlParams = new URLSearchParams(window.location.search);
    const idOggetto = urlParams.get('id');
    renderContenuto();
    renderArgomenti();

    function renderContenuto() {
        var urlSezione = apibase.baseurl + '10500/' + idOggetto;
        return $.ajax({
            url: urlSezione,
            success: function (result) {
                if (!result) {
                    window.location = process.env.BASE_URL + "notFound.html";
                }
                $("#title").html(result.title);
                var data = StringRenderer.renderDataTestuale(result.properties.data_pubblicazione)
                    + ', ' + result.properties.data_pubblicazione_time.slice(0, 5);
                $("#subtitle").text(data);
                $("#container_base").append(renderImmagine(result));
                $("#container_base").append('<span class="light-obj-name h6">' + result.properties.intestazione + '</span>');
                $("#container_base").append('<p>' + result.properties.corpo + '</p>');
                $("#container_base").append('<div class="font-weight-bold text-secondary">' + (result.properties.autore || '') + '</div>');
                $("#container_base").append(renderGalleriaVideo(result));
                $("#container_base").append(renderVideoYt(result));
                $("#container_base").append(renderGalleriaFoto(result));
                $("#container_base").append(renderImmagini(result));
                $("#container_base").append(renderAllegati(result));
                $("#container_base").append(Pagina.renderLink(result));
                if (result.properties.approfondimento == 'T') {
                    $('#bread').append('<li class="breadcrumb-item">\
                                            <span class="separator">/</span>\
                                            <a href="notizie.html?tipo=approfondimento">Approfondimenti</a>\
                                        </li>')
                }
                renderNotizieCorrelate(result);
            }, error: function (res_err) {
                window.location = process.env.BASE_URL + "notFound.html";
            }
        });
    }

    function renderArgomenti() {
        var urlArgomenti = apibase.baseurl + '10503';
        return $.ajax({
            url: urlArgomenti,
            success: function (result) {
                if (result && result.content) {
                    let pesoTotale = 0;
                    let argomenti = [];
                    result.content.forEach(function (element) {
                        pesoTotale = Math.max(Number(element.properties.peso), pesoTotale);
                    });
                    result.content.forEach(function (element) {
                        let argomento = new Argomento();
                        var iconLink = "";
                        if (element.properties.icon.indexOf("it-") != -1) {
                            iconLink = process.env.BASE_URL + "svg/sprite.svg#" + element.properties.icon;
                        }
                        if (element.properties.icon.indexOf("ba-") != -1) {
                            iconLink = process.env.BASE_URL + "svg/ba-sprite.svg#" + element.properties.icon;
                        }
                        argomento.icona = iconLink;
                        argomento.id = element.id;
                        argomento.nome = element.title;
                        argomento.peso = element.properties.peso / pesoTotale;
                        argomenti.push(argomento);
                    });
                    $('#argomenti').html('<div class="row">' + vistaArgomenti.renderArgomenti(argomenti) + '</div>');
                }
            }
        });
    }

    function renderNotizieCorrelate(notizia) {
        NotizieCorrelate.render(notizia).then(function (html) {
            if (html && html.length) {
                $("#contenutiCorrelati").html(html);
                $("#sezioneContenutiCorrelati").removeClass('d-none');
            }
        });
    }


    function renderAllegati(pagina) {
        if (pagina.components.allegati && pagina.components.allegati.components.length) {
            let righeAllegati = '';
            pagina.components.allegati.components.forEach(function (allegato) {
                righeAllegati += '\
                <div class="row  bg-title-area m-2">\
                    <div class="col">\
                        <a href="' + baseImgUrl + allegato.properties.address + '" target="_blank"> ' + allegato.title + ' </a>\
                    </div>\
                </div>';
            });
            let allegatiHtml = '\
                <hr class="border-news mb-0"/>\
                <h6 class="dark-obj-name ml-2 mt-2">' + pagina.components.allegati.label + '</h6>\
                <div class="container-fluid mb-4 " id="row_allegato">' + righeAllegati + '</div>'
            return allegatiHtml;
        }
        return '';
    }

    function renderImmagine(result) {
        if (result.components.immagine != undefined) {
            if (result.components.immagine.components.length > 0) {
                let righeImmagini = '';
                result.components.immagine.components.forEach(function (immagine) {
                    var iconLink, thumb;
                    try {
                        var file = immagine.components.file.components[0];
                        iconLink = baseImgUrl + file.properties.address;
                        thumb = baseImgUrl + file.properties.thumbaddress;
                        iconLink = Utils.creaPlaceholder(iconLink, '//via.placeholder.com/1280x768');
                        thumb = Utils.creaPlaceholder(thumb, '//via.placeholder.com/512x320');
                        var content = ' <figure>\
                                            <img class="img-fluid" src="' + iconLink + '" data-src="' + thumb + '" alt="' + immagine.title + '" title="' + immagine.title + '" />\
                                            <figcaption class="figure-caption font-weight-bold">' + (immagine.properties.didascalia || '') + '</figcaption>\
                                        </figure>\
                                        <hr  class="border-news mb-0" />';
                        righeImmagini += content;
                    } catch (e) {
                        console.log(e);
                    }
                });
                return '\
                    <div class="row">\
                        <div class="container " id = "row_immagine" > '+ righeImmagini + '</div>\
                    </div > ';
            }
        }
        return '';
    }

    function renderGalleriaFoto(result) {
        if (result.components.galleria && result.components.galleria.components.length) {
            let html = [];
            result.components.galleria.components.forEach(function (galleria) {
                let immagine = {};
                if (galleria.components.immagine && galleria.components.immagine.components.length) {
                    immagine = {
                        titolo: galleria.components.immagine.components[0].title,
                        src: baseImgUrl + galleria.components.immagine.components[0].components.file.components[0].properties.thumbaddress
                    }
                    immagine.src = Utils.creaPlaceholder(immagine.src, '//via.placeholder.com/512x320');
                }
                let anteprima = new AnteprimaGalleria();
                anteprima.srcImmagine = immagine.src;
                anteprima.titoloImmagine = immagine.titolo;
                anteprima.linkOggetto = "galleria-immagini.html?id=" + galleria.id;
                anteprima.titoloOggetto = galleria.title;
                html.push('<div class="col-12">' + VistaGallerie.renderAnteprimaGalleria(anteprima) + '</div>');

            });
            return '<hr class="border-news mb-0"/>\
                <h6 class="dark-obj-name ml-2 mt-2">' + result.components.galleria.label + '</h6>\
                <div class="row ml-3">' + html.join('') + '</div>';
        }
        return '';
    }

    function renderImmagini(result) {
        let galleria_immagini = result.components.immagini;
        if (galleria_immagini && galleria_immagini.components.length) {
            let immagini = [];
            galleria_immagini.components.forEach(function (resultImmagine) {
                let immagine = new AnteprimaImmagine();
                immagine.titolo = resultImmagine.title;
                immagine.idGalleria = 'galleria' + idOggetto;
                immagine.src = Utils.creaUrlImmagine(resultImmagine.components.file.components[0].properties.address);
                immagine.thumbnail = Utils.creaUrlImmagine(resultImmagine.components.file.components[0].properties.thumbaddress);
                immagini.push('<div class="col-6 col-lg-4">' + VistaGallerie.renderAnteprimaImmagine(immagine) + '</div>');
            });
            return '<hr class="border-news mb-0"/>\
                            <h6 class="dark-obj-name ml-2 mt-2">' + result.components.immagini.label + '</h6>\
                            <div class="row ml-3">' + immagini.join('') + '</div>';
        }
        return '';
    }

    function renderVideoYt(result) {
        let videoResult = result.components.video;
        if (videoResult && videoResult.components.length) {
            let html = [];
            videoResult.components.forEach(function (video) {
                html.push('<div class="col-12"><div>' + video.properties.iframe + '</div>' + video.title + '</div>');
            });
            return '<hr class="border-news mb-0"/>\
                <h6 class="dark-obj-name ml-2 mt-2">' + result.components.video.label + '</h6>\
                <div class="yt-video row ml-3">' + html.join('') + '</div>';
        }
        return '';
    }

    function renderGalleriaVideo(result) {
        let videoResult = result.components.video_ws;
        if (videoResult && videoResult.components.length) {
            let html = [];
            videoResult.components.forEach(function (video) {
                let immagine = {};
                if (video.components.image && video.components.image.components.length) {
                    immagine = {
                        titolo: video.components.image.components[0].title,
                        src: Utils.creaUrlImmagine(video.components.image.components[0].properties.thumbaddress)
                    }
                    immagine.src = Utils.creaPlaceholder(immagine.src, '//via.placeholder.com/512x320');
                }
                let anteprima = new AnteprimaGalleria();
                anteprima.srcImmagine = immagine.src;
                anteprima.titoloImmagine = immagine.titolo;
                anteprima.linkOggetto = "video.html?id=" + video.id;
                anteprima.titoloOggetto = video.title;
                html.push('<div class="col-12">' + VistaGallerie.renderAnteprimaGalleria(anteprima) + '</div>');
            });
            return '<hr class="border-news mb-0"/>\
                <h6 class="dark-obj-name ml-2 mt-2">' + result.components.video_ws.label + '</h6>\
                <div class="row ml-3">' + html.join('') + '</div>';
        }
    }
    return '';
}


